.tictactoe {
    height: 100%;
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}

.tictactoe header {
    padding-top: 30px;
    font-size: larger;
    font-weight: 600;
}


ol,
ul {
    padding-left: 30px;
}

.boardrow:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
}

.square {
    background: orange;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: flex;
    flex-direction: row;
}

.gameinfo {
    margin-left: 20px;
}